.item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    margin-bottom: 20px;
    padding-left: 20px;
    height: 100px;
    border: 0;
    background-color: #fff;
    font-size: 30px;
    font-weight: 500;
    border-radius: 3px;
    box-shadow: 0px 2px 2px 2px rgba(151,151,165,0.46);
    color: #25266b;
}
.item:hover {
    background-color: #d1cfcf;
    box-shadow: 2px 2px 2px 2px rgba(247, 247, 248, 0.548);
    color: #ffffff;
} 
  
.avatar {
    width: 80px;
    height: 80px;
    border-radius: 15px;
    margin-right: 30px;
}
 

.statusOnline,
.statusOffline {
      display: block;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background-color: green;
      margin: 0 15px 0 15px;
}
  
.statusOffline {
      background-color: red;
}