.description {
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    
}
.avatar {
    width: 150px;
    height: 150px;
    max-width: 100%;
    padding: 2px;
    margin-bottom: 10px;
    border-radius: 50%;
    border: 2px solid #2c4c5f;
}
.name {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 20px;
    color:  #142544;
}
  
.tag {
    margin-bottom: 20px;
    font-size: 18px;
    color:  #142544;
}
.location {
    margin-bottom: 20px;
    font-size: 18px;
    color:  #2c4c5f;
}